.form {
    width: 100%;
    max-width: 500px;
    margin: 10px auto;
}
.form__header {
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    text-transform: uppercase;
}
.form__fieldset {
    border: 2px solid black; 
    border-radius: 5px; 
    padding: 10px;   
    margin: 10px 0px;
}
.form__legend {
    background-color: teal; 
    color: white;
    border: 2px solid black; 
    border-radius: 5px; 
    padding: 5px;   
}
.form__labelText {  
    max-width: 150px;
    width: 100%;
    display: inline-block; 
    margin-right: 10px;
}
.form__field {
    border: 1px solid grey; 
    border-radius: 5px;   
    padding: 7px;
    max-width: 200px;
    width: 100%;
    margin-top: 8px;
}
.form__button {
    width: 100%; 
    border: 2px solid black; 
    background-color: teal; 
    color: white;
    border-radius: 5px; 
    padding: 10px;
    margin-top: 10px;
    cursor: pointer;
}
.form__button:hover {
    background-color: hsl(180, 100%, 35%); 
}
.form__button:active {
    background-color: hsl(180, 100%, 45%); 
    border: none;
}
.form__legend {
    font-family: Arial, Helvetica, sans-serif;
    text-align: center;
    text-transform: uppercase;
    background-color: teal; 
    color: white;
    border: 2px solid black; 
    border-radius: 5px; 
    padding: 5px;  
}
.form__info{
    font-family: 'Times New Roman', Times, serif;
    font-size: px;
    text-align: center;
    padding: 10px;
    margin-top: 5px;
}
